<!--
 * @Author: Marlon
 * @Date: 2020-02-24 21:50:15
 * @LastEditors: Marlon
 * @LastEditTime: 2020-02-25 13:24:48
 * @Description: 采集数据统计
 -->

<template>
  <div class="data-statistics">
    <div class="data-statistics-top">
      <ul class="area-title">
        <li class="area-title-1">
          <span>采集任务：</span>
          <span>北京互联互通任务采集</span>
        </li>
      </ul>
      <ul class="area-title">
        <li class="area-title-1">
          <span>源库：</span>
          <span>共有 50张表 采集了 40张表的数据</span>
        </li>
        <li class="area-title-2">
          <span>目标库：</span>
          <span>共有100张表 本次任务同步数据涉及40张表</span>
        </li>
      </ul>
      <div class="area-table">
        <TableDefault TId="1" :TTitle="tableTitle" :TData="tableData" :TIsShowPage="true"></TableDefault>
      </div>
    </div>
  </div>
</template>

<script>
import mixin from "mixin";

export default {
  mixins: [mixin],
  data() {
    return {
      tableTitle: [
        {
          prop: "name",
          label: "所属项目"
        },
        {
          prop: "date",
          label: "共维护更新次数"
        },
        {
          prop: "address",
          label: "对应数据源(物理库)"
        },
        {
          prop: "address",
          label: "对应数据源(物理库)"
        }
      ],
      tableData: [
        {
          date: "122",
          name: "通用",
          address: "45"
        },
        {
          date: "122",
          name: "海淀",
          address: "45"
        },
        {
          date: "122",
          name: "山西",
          address: "45"
        },
        {
          date: "122",
          name: "济南",
          address: "45"
        },
        {
          date: "122",
          name: "山东",
          address: "45"
        },
        {
          date: "122",
          name: "贵州",
          address: "45"
        },
        {
          date: "122",
          name: "福州",
          address: "45"
        },
        {
          date: "122",
          name: "泰安",
          address: "45"
        },
        {
          date: "122",
          name: "南京",
          address: "45"
        },
        {
          date: "122",
          name: "广州",
          address: "45"
        }
      ]
    };
  },
  methods: {}
};
</script>

<style lang="less" scoped>
.data-statistics {
  padding: 20px;
  min-width: 1300px;
  width: calc(100% - 1px);
  height: 100%;
  box-sizing: border-box;
  border: 1px solid red;
  overflow: auto;
  display: flex;
  flex-direction: column;
  .area {
    &-title {
      height: 32px;
      line-height: 30px;
      margin: 5px 30px;
      &-1 {
        float: left;
      }
      &-2 {
        margin-left: 400px;
      }
    }
  }
  &-top {
    width: 100%;
    height: 100px;
    box-sizing: border-box;
    border: 1px solid red;
    flex: 1;
    overflow: hidden;
    .area-table {
      width: 100%;
      height: calc(100% - 80px);
      overflow: auto;
    }
  }
}
</style>



